* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body{
  font-family: 'montseratt',sans-serif;

}

.App{
    
  background-image: url('./assets/artem-kulastinen-9CUAf5I79b0-unsplash.jpg');
  background-size: cover;
  background-position: bottom;
  -webkit-transition: 0.4 ease-out;
  -o-transition: 0.4 ease-out;
  transition: 0.4 ease-out;

}

.App.warm{
  background-image: url('./assets/1051978-download-sunny-day-wallpaper-2560x1600-smartphone.jpg');
}

main{
  min-height: 100vh;
  background-image: -webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,0.2)),to(rgba(0,0,0,0.75)));
  background-image: -webkit-linear-gradient(top,rgba(0,0,0,0.2),rgba(0,0,0,0.75));
  background-image: -o-linear-gradient(top,rgba(0,0,0,0.2),rgba(0,0,0,0.75));
  background-image: linear-gradient(to bottom,rgba(0,0,0,0.2),rgba(0,0,0,0.75));
  padding:25px;
}

.search-box{
  width:100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;

  -webkit-appearance: none;

     -moz-appearance: none;

          appearance: none;
  background: none;

  border: none;
  outline: none;

  background-color: rgba(255,255,255,0.5);
  border-radius: 0px 0px 16px 16px;
  margin: -25px;

  -webkit-box-shadow: 0px 5px rgba(0,0,0,0.2);

          box-shadow: 0px 5px rgba(0,0,0,0.2);

  color: #313131;
  font-size: 20px;

  -webkit-transition: 0.4 ease ;

  -o-transition: 0.4 ease ;

  transition: 0.4 ease ;
}

.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.75);
}

.location-box .location{
  color: #ffffff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);

}
.location-box .date{
  color: #ffffff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.weather-box{
  text-align:center;
}
.weather-box .temp{
  position:relative;
  display: inline-block;
  margin:30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;

  padding:15px 25px;

  color:#ffffff;
  font-size:102px;
  font-weight:900;

  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
  -webkit-box-shadow: 3px 6px rgba(0,0,0,0.2);
          box-shadow: 3px 6px rgba(0,0,0,0.2);

}

.weather-box .weather{
  color: #ffffff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}




